<template>
  <div class="read" :style="{backgroundColor:color}">
    <div
      class="text-box"
      ref="text_box"
      @click.stop="toggleShow(true)"
      @touchstart="getStartPageY($event)"
      @touchmove="getMovePageY($event)"
      @touchend="moveEnd"
    >
      <h5 class="text-title" :style="{fontSize: fontSize_tit + size +'px'}">{{book.title}}</h5>
      <div class="vip-tip" v-if="book.isVip">
        <van-divider :style="{ color: '#666', borderColor: '#666'}">后续为付费章节需购买后方可继续阅读</van-divider>
        <div>下载最新版追书神器APP体验更多功能</div>
      </div>
      <div v-else class="text-content" :style="{fontSize: fontSize + size +'px'}">
        <p v-for="(item , index) in book.cpContent" :key="index">{{item}}</p>
      </div>
    </div>
    <!-- 上弹出框 -->
    <van-popup class="popup-top" :overlay="false" v-model="isShow" position="top">
      <van-nav-bar :title="bookMsg.bookName" left-text="返回" left-arrow fixed @click-left="goPage">
        <template #right>
          <svg
            t="1594949373600"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="15150"
            width="24"
            height="24"
            @click="goPage('Bookrack')"
          >
            <path
              d="M264.154 952.655H64.31V224.11h199.844v728.545h-0.001z m-34.2-691.413H98.816v67.142h131.14v-67.142z m96.516-196.7h198.267v888.045H326.47V64.542z m168.989 34.214H360.143v69.235h135.315V98.756z m459.399 794.012L764.845 954.68 539.126 261.973l190.01-61.912 225.722 692.707zM708.125 245.964L583.438 286.59l20.798 63.834 124.686-40.627-20.797-63.833z m0 0"
              p-id="15151"
              fill="#B0C4DE"
            />
          </svg>
          <svg
            t="1596588838793"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="5289"
            width="24"
            height="24"
            @click="goPage('Bookstore')"
          >
            <path
              d="M691.2 691.2m-332.8 0a332.8 332.8 0 1 0 665.6 0 332.8 332.8 0 1 0-665.6 0Z"
              fill="#B0C4DE"
              p-id="5290"
            />
            <path
              d="M730.624 947.712H294.912c-96.768 0-123.392-32.256-123.392-133.12V358.4c0-14.336 11.264-25.6 25.6-25.6s25.6 11.264 25.6 25.6v456.192c0 73.216 3.072 81.92 72.192 81.92h435.712c69.12 0 81.92-8.192 81.92-81.92V358.4c0-14.336 11.264-25.6 25.6-25.6s25.6 11.264 25.6 25.6v456.192c0 100.864-36.352 133.12-133.12 133.12z"
              fill="#B0C4DE"
              p-id="5291"
            />
            <path
              d="M620.032 934.912c-7.168 0-12.8-5.632-12.8-12.8v-243.2c0-25.6-5.12-26.112-33.28-26.112h-122.88c-28.16 0-23.552 0.512-23.552 26.112v243.2c0 7.168-5.632 12.8-12.8 12.8s-12.8-5.632-12.8-12.8v-243.2c0-44.032 11.776-51.712 49.152-51.712h122.88c37.376 0 58.88 7.68 58.88 51.712v243.2c0 7.168-5.632 12.8-12.8 12.8zM90.112 434.688c-7.68 0-15.36-3.584-20.48-10.24-8.704-11.264-6.144-27.136 5.12-35.84l422.912-317.952c11.264-8.704 27.136-6.144 35.84 5.12s6.144 27.136-5.12 35.84L105.472 429.568c-4.608 3.584-9.728 5.12-15.36 5.12z"
              fill="#B0C4DE"
              p-id="5292"
            />
            <path
              d="M935.424 434.688c-5.632 0-10.752-2.048-15.36-5.12L497.664 111.616c-11.264-8.704-13.824-24.576-5.12-35.84s24.576-13.312 35.84-5.12l422.912 317.952c11.264 8.704 13.824 24.576 5.12 35.84-5.632 6.656-13.312 10.24-20.992 10.24zM287.232 550.4c-7.168 0-12.8-5.632-12.8-12.8V392.192c0-4.096-0.512-7.68 2.56-10.24l166.912-129.024c5.632-4.096 12.8-3.072 17.408 2.56 4.096 5.632 5.632 13.824 0 17.92L300.032 398.336V537.6c0 7.168-5.632 12.8-12.8 12.8z m0 64c-7.168 0-12.8-5.632-12.8-12.8V588.8c0-7.168 5.632-12.8 12.8-12.8s12.8 5.632 12.8 12.8v12.8c0 7.168-5.632 12.8-12.8 12.8z"
              fill="#B0C4DE"
              p-id="5293"
            />
          </svg>
        </template>
      </van-nav-bar>
    </van-popup>
    <!-- 下弹出框 -->
    <van-popup
      class="popup-bottom"
      :overlay="false"
      v-model="isShow"
      position="bottom"
      :style="{ height: '30%' }"
    >
      <div class="title-box clearfix">
        <div class="prev fl" @click="toggleBook">＜上一章</div>
        <div class="catalog-text fl" @click.stop="toggleShow(false)">目录</div>
        <div class="next fr" @click="toggleBook('next')">下一章＞</div>
      </div>
      <div class="text-font clearfix">
        <span class="fl">字号</span>
        <span class="minus fl" @click="changeFont">Aa-</span>
        <van-slider
          class="text-slider fl"
          v-model="size"
          :min="-2"
          :max="14"
          active-color="lightsteelblue"
        />
        <span class="add fl" @click="changeFont('+')">Aa+</span>
      </div>
      <div class="color-box clearfix">
        <span class="fl bg-text">背景</span>
        <div class="bg-box fl">
          <span
            class="bg"
            :class="[color.isActive ? 'active' : '']"
            v-for="(color,i) in colorList"
            :key="i"
            :style="{background:color.color}"
            @click="changeColor(i)"
          ></span>
        </div>
      </div>
    </van-popup>
    <!-- 左弹出框 -->
    <van-popup class="popup-left" v-model="isLeft" position="left">
      <div class="catalog-box">
        <div class="catalog-tit">{{bookMsg.bookName}}</div>
        <div class="name">{{bookMsg.name}}</div>
        <div class="num">共{{maxLength}}章</div>
      </div>
      <van-list
        class="catalog-content"
        v-model="load.loading"
        :finished="load.finished"
        :finished-text="load.loadingMsg"
        @load="onLoad"
      >
        <div
          :class="[j == bookIndex ? 'active' : '']"
          v-for="(li,j) in catalog"
          :key="j"
          @click="click(j)"
        >{{li.title}}</div>
      </van-list>
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bookMsg: {
        // 小说id
        id: "",
        // 获取内容需要小说link值
        link: "",
        // 书名
        bookName: "",
        // 作者名
        name: "",
        url: ""
      },
      // 是否显示上下弹出框
      isShow: false,
      // 是否显示左弹出框
      isLeft: false,
      // 小说内容
      book: {},
      //   默认标题字体大小
      fontSize_tit: 28,
      //  默认字体
      fontSize: 20,
      //字体倍数
      size: 2,
      //  默认背景颜色
      color: "#FAF9F7",
      //  颜色列表
      colorList: [
        {
          color: "#FAF9F7",
          isActive: true
        },
        {
          color: "#FBF5ED",
          isActive: false
        },
        {
          color: "#E6EBEF",
          isActive: false
        },
        {
          color: "#F5EDEF",
          isActive: false
        },
        {
          color: "#0C0C0C",
          isActive: false
        },
        {
          color: "#B8CD8D",
          isActive: false
        }
      ],
      // 目录列表,
      // 所有书籍列表
      allCatalog: [],
      // 默认加载书籍列表
      catalog: [],
      // 默认加载目录数量
      loadCount: 50,
      load: {
        // 是否加载 false表示加载
        // true为停止加载
        loading: false,
        finished: false,
        loadingMsg: "已经到底啦"
      },
      // 章节索引值
      bookIndex: 0,
      // 总章节
      maxLength: 0,
      // 手指按下位置
      start: 0,
      move: 0
    };
  },
  created() {
    // 获取link值
    this.bookIndex = this.$route.query.linkIndex;
    this.bookMsg.link = decodeURIComponent(this.$route.query.link);
    this.bookMsg.id = this.$route.query.id;
    this.bookMsg.bookName = this.$route.query.book;
    this.bookMsg.name = this.$route.query.name;
    // 加载小说
    setTimeout(this.getBookContent(), 0);
    // 加载目录
    this.getCatalog();
  },
  methods: {
    // 获取小说内容
    getBookContent() {
      // this.backTop();
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url:
          "http://novel.kele8.cn/chapters/" +
          encodeURIComponent(this.bookMsg.link)
      })
        .then(result => {
          this.$toast.clear();
 
          if (result.status == 200) {
            this.book = result.data.chapter;
            this.book.cpContent = this.book.cpContent.split("\r\n");
            for (let i = 0; i < this.book.cpContent.length; i++) {
              this.book.cpContent[i] = this.book.cpContent[i].replace(
                /\s+/g,
                ""
              );
            }
            this.$refs.text_box.scrollTop = 0;
          }
        })
        .catch(err => {
          this.$toast.clear();
 
        });
    },
    // 显示工具栏 | 目录
    toggleShow(bool) {
      if (bool) {
        this.isShow = !this.isShow;
      } else {
        this.isLeft = true;
        this.isShow = false;
      }
    },
    // 页面跳转
    goPage(name) {
      if (name == "Bookrack" || name == "Bookstore") {
        this.$router.push({ name: name });
      } else {
        this.$router.go(-1);
      }
    },
    // 改变字体大小
    changeFont(val) {
      if (val == "+") {
        this.size >= 14 ? 14 : (this.size += 2);
      } else {
        this.size <= -2 ? -2 : (this.size -= 2);
      }
    },
    // 切换背景颜色
    changeColor(index) {
      if (this.colorList[index].isActive) {
        return;
      }
      for (let i = 0; i < this.colorList.length; i++) {
        if (this.colorList[i].isActive) {
          this.colorList[i].isActive = false;
        }
      }
      this.colorList[index].isActive = true;
      this.color = this.colorList[index].color;
    },
    // 获取目录列表,加载目录
    getCatalog() {
      this.axios({
        method: "get",
        url:
          "http://novel.kele8.cn/book-sources?view=summary&book=" +
          this.bookMsg.id
      })
        .then(result => {
 
          if (result.status == 200) {
            let bookId = result.data[0]._id;
            this.axios({
              method: "get",
              url: "http://novel.kele8.cn/book-chapters/" + bookId
            })
              .then(result => {
 
                if (result.status == 200) {
                  this.allCatalog = result.data.chapters;
                  this.maxLength = this.allCatalog.length - 1;
                  this.catalog = this.allCatalog.slice(0, this.loadCount);
                  if (this.allCatalog.length > 0) {
                    this.load.loading = false;
                  } else {
                    this.load.loading = true;
                    this.load.finished = true;
                  }
                  if (this.catalog.length == 0) {
                    this.load.loadingMsg = "";
                  }
                }
              })
              .catch(err => {
 
              });
          }
        })
        .catch(err => {
 
        });
    },
    // 懒加载目录数据
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        // 添加章节
        this.catalog.push(...this.allCatalog.splice(0, this.loadCount));
        // 默认加载数据
        this.load.loading = false;
        // 如果没有数据则停止加载
        if (this.allCatalog.length == 0) {
          this.load.loading = true;
          this.load.finished = true;
        }
      }, 500);
    },
    // 图片解码
    getImg(val) {
      return unescape(val).slice(7);
    },
    // 跳转到对应章节
    click(index) {
      this.bookIndex = index;
      this.bookMsg.link = encodeURIComponent(this.catalog[this.bookIndex].link);
      this.getBookContent();
      this.isLeft = false;
    },
    // 切换上下章
    toggleBook(type) {
      if (type == "next") {
        if (this.bookIndex > this.catalog.length - 1) {
          this.onLoad();
        }
        this.bookIndex > this.maxLength ? this.maxLength : this.bookIndex++;
      } else {
        this.bookIndex <= 0 ? 0 : this.bookIndex--;
      }
      this.bookMsg.link = this.catalog[this.bookIndex].link;
      this.getBookContent();
    },
    getStartPageY(e) {
      this.move = e.touches[0].pageY - this.start;
    },
    // 获取开始的坐标值
    getMovePageY(e) {
      this.start = e.touches[0].pageY;
    },
    // 自动切换下一章
    moveEnd() {
      let text_box = this.$refs.text_box;
      let offsetHeight = text_box.offsetHeight;
      if (text_box.scrollHeight - text_box.scrollTop == offsetHeight) {
        if(this.move > 200){
          this.$toast("下一章加载ing...");
          setTimeout(()=>{
            this.toggleBook('next')
          },1000)
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
.v-box {
  height: 40px;
  line-height: 40px;
  margin-bottom: 5px;
  font-size: 16px;
}
.read {
  width: 100%;
  height: 100%;
  position: fixed;
  .text-box {
    width: 100%;
    height: 100%;
    padding: 20px;
    box-sizing: border-box;
    color: #333;
    overflow-y: auto;
    &::-webkit-scrollbar {
      display: none;
    }
    .text-title {
      padding: 0;
      margin: 20px 0;
      font-weight: normal;
    }
    .text-content {
      line-height: 1.5;
      text-align: justify;
      p {
        text-indent: 36px;
        padding: 0;
        margin: 0;
        word-wrap: break-word;
        word-break: break-all;
      }
    }
    .vip-tip {
      margin-top: 60%;
      color: #555;
      text-align: center;
      font-size: 16px;
    }
  }
  .popup-top {
    height: 46px;
  }
  .popup-bottom {
    color: #666;
    padding: 20px;
    box-sizing: border-box;
    font-size: 14px;
    .title-box {
      .v-box;
      display: flex;
      justify-content: space-around;
      div {
        flex: 1;
        &:nth-child(3) {
          text-align: right;
        }
        &.prev,
        &.next {
          color: lightsteelblue;
        }
      }
      .catalog-text {
        text-align: center;
        // box-shadow: 1px 1px 3px 1px #ddd;
        border-radius: 20px;
        color: steelblue;
      }
    }
    .text-font {
      padding: 10px 0;
      line-height: 18px;
      display: flex;
      align-items: center;
      margin-bottom: 15px;
      span {
        width: 50px;
        box-sizing: border-box;
        font-size: 16px;
        text-align: center;
        &:nth-child(1) {
          text-align: left;
        }
      }
      .text-slider {
        width: calc(~"100% - 150px");
        margin: 0 10px;
      }
    }
    .color-box {
      .v-box;
      .bg-text {
        width: 50px;
      }
      .bg-box {
        width: calc(~"100% - 50px");
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        span {
          height: 100%;
          width: 40px;
          border-radius: 50%;
          border: 2px solid transparent;
          transition: border 0.5s;
          box-shadow: 1px 1px 1px #ddd;
          box-sizing: border-box;
          &.active {
            border: 2px solid lightsteelblue;
          }
        }
      }
    }
  }
  .popup-left {
    font-size: 18px;
    height: 100%;
    width: 80%;
    padding: 10px;
    box-sizing: border-box;
    .catalog-box {
      width: 100%;
      height: 115px;
      .catalog-tit {
        font-size: 22px;
        color: steelblue;
        margin-top: 15px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
      .name {
        color: #666;
        margin: 5px 0 10px 0;
      }
      .num {
        font-size: 14px;
        color: #888;
      }
    }
    .catalog-content {
      height: calc(~"100% - 130px");
      overflow-y: auto;
      line-height: 2;
      padding: 5px 10px;
      box-sizing: border-box;
      color: #666;
      box-shadow: inset 1px 1px 5px 1px #ddd;
      border-radius: 10px;
      .active {
        color: steelblue;
        transform: scale(1.05);
        transition: transform 0.3s;
      }
    }
  }
}
</style>